<template>
    <div class="container v50 d-flex justify-content-center align-items-center">
        <div class="row justify-content-center w-100">
            <div class="col-md-6">
                <div class="card shadow-sm">
                    <div class="card-body">
                        <h5 class="card-title text-center mb-4">Verifica tu código</h5>
                        <form @submit.prevent="submitCode">
                            <div class="d-flex justify-content-between mb-3">
                                <input v-for="(digit, index) in digits" :key="index" type="text"
                                    class="form-control text-center mx-1" maxlength="1" pattern="[0-9]*"
                                    inputmode="numeric" v-model="digits[index]" @input="onInput(index)"
                                    @keydown.backspace="onBackspace(index)" @keydown.left="focusPrev(index)"
                                    @keydown.right="focusNext(index)" ref="digitRefs" />
                            </div>
                            <button type="submit" class="btn btn-primary w-100" :disabled="loading">
                                {{ loading ? 'Verificando...' : 'Validar código' }}
                            </button>
                            <div v-if="error" class="alert alert-danger mt-3">
                                {{ error }}
                            </div>
                            <div v-if="success" class="alert alert-success mt-3">
                                ¡Código verificado correctamente!
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
     <!-- Modal de error -->
    <div v-if="showModal" class="modal">
        <div class="modal-content">
            <h3>{{ errorMessage }}</h3>
            <br>
            <button class="btn-black px-3" @click="showModal = false">{{ t('account.btnCerrar') }}</button>
        </div>
    </div>
</template>

<script setup>
import { ref, nextTick } from 'vue'
import { API_BASE_URL } from '@/config/api'
import { useRouter } from 'vue-router' //redirect
import { useI18n } from 'vue-i18n' //multilenguaje

//multilenguaje
const { t } = useI18n() 
const digits = ref(['', '', '', '', '', ''])
const digitRefs = ref([])
const loading = ref(false)
const error = ref('')
const success = ref(false)
//router
const router = useRouter()

//modal
    const errorMessage = ref('')
    const showModal = ref(false)

const onInput = index => {
    const val = digits.value[index]
    if (!/^\d$/.test(val)) {
        digits.value[index] = ''
        return
    }
    if (index < 5) {
        nextTick(() => digitRefs.value[index + 1].focus())
    }
}

const onBackspace = index => {
    if (digits.value[index] === '' && index > 0) {
        nextTick(() => digitRefs.value[index - 1].focus())
    }
}

const focusPrev = index => {
    if (index > 0) {
        digitRefs.value[index - 1].focus()
    }
}

const focusNext = index => {
    if (index < 5) {
        digitRefs.value[index + 1].focus()
    }
}

const submitCode = async () => {
    error.value = ''
    success.value = false
    loading.value = true

    const code = digits.value.join('')
    await new Promise(resolve => setTimeout(resolve, 1000))

    try {
        const response = await fetch(`${API_BASE_URL}/api/usuarios/VerifyCode`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                email: localStorage.getItem('email'),
                code: code
            })
        })
      
        const data = await response.json()
       showModal.value = true
        if (data.status == 1) {
            localStorage.setItem('token', localStorage.getItem('email'))
            router.push('/home') // Redirige a la verificación de codigo
        } else {
            errorMessage.value = data.message
            showModal.value = true
        }
    } catch (error) {
        errorMessage.value = 'Error de conexión con el servidor'
    }

    loading.value = false
}
</script>

<style scoped>
input[type="text"] {
    width: 3rem;
    height: 3rem;
    font-size: 1.5rem;
}

.v50 {
    height: 50vh !important;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  padding:20px
}
.modal-content {
  background: white;
  padding: 40px;
  border-radius: 8px;
}
</style>
